.outside{
    display: flex;
    width:100%;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
}

img{
    width:40vw;
    margin-top: 20px;
    margin-bottom: 20px;
}


.cent{
    align-items: center;
}


.memeHolder{
    position:relative;
    display:flex;
    justify-content: center;
    
    width: fit-content;
    height:fit-content;

}

.memeTexttop{
    font-size: 46px;
    color: white;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    outline: 1px;
    position: absolute;
    z-index: 2;
    text-shadow: -1px 1px 0 #000,
				  1px 1px 0 #000,
				 1px -1px 0 #000,
				-1px -1px 0 #000;
}
.memeTextbottom{
    z-index:2;
    font-size: 46px;
    color: white;
    font-family:Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
    outline: 1px;
    position:absolute;
    bottom:0;
    z-index: 2;
    text-shadow: -1px 1px 0 #000,
				  1px 1px 0 #000,
				 1px -1px 0 #000,
				-1px -1px 0 #000;
}

.divButton{
    margin-top:10px;
    background-color: antiquewhite;
}